import {QrScanner} from '@yudiel/react-qr-scanner';

import React from 'react'
import LayoutWithSidebar from '../components/LayoutWithSidebar'
import LaunchLayout from '../components/LaunchLayout'


import Button from '../components/Button';

import { useNavigate,  } from "react-router-dom";

import config from '../config';

import {motion} from 'framer-motion'

import spiderRedImg from '../assets/images/spider-red.png';
import spiderWhiteImg from '../assets/images/spider-white.png';
import cameraImg from '../assets/images/icon-camera.png';

import { useAtom } from 'jotai'
import { currentMapAtom, currentCharacterAtom, lastQRAtom } from '../atoms/index.js';


const forceCamera = () => {
  if(!navigator.mediaDevices) return
  navigator.mediaDevices
  .getUserMedia({video: true, audio: false})
  .then((stream) => {
    var video = document.querySelector('video');
    video.src = window.URL.createObjectURL(stream);
    video.onloadedmetadata = function(e) {
       // Do something with the video here.
    };
  })
  .catch((err) => {
    /* handle the error */
  });

}

export const ScanQR = (props) => {
  let navigate = useNavigate();

  let [qrScannerError, setQRScannerError] = React.useState("no error")
  const [currentMap, setCurrentMap] = useAtom(currentMapAtom)
  const [currentCharacter, setCurrentCharacter] = useAtom(currentCharacterAtom)
  const [lastQR, setLastQRMap] = useAtom(lastQRAtom)
  
  React.useEffect(() => {
    console.log("navigator", navigator)
    //forceCamera()
    
    console.log("currentMap", currentMap)
  }, [])

  const scan = (url_str) => {
    let url = null;

    try {
      url = new URL(url_str)
    } catch (e){}

    if(!url){
      alert("not valid qr")
      return
    }

    let params = new URLSearchParams(url.search);
    let param_map = params.get("m");
    let param_character = params.get("p");

    let param_next_map = params.get("nm");
    let param_selected_character = params.get("ps");
    console.log("M PARAM test", param_map, param_character)
    
    if(param_map){
      if(!param_character) return

      let next = null 
      if(param_character == 'm'){
        setCurrentCharacter('MILES')
        next = config.PATHS.MILES[0].key
      } else {
        setCurrentCharacter('PETER')        
        next = config.PATHS.PETER[0].key
      }

      console.log("next", next)
      setCurrentMap(next)

      navigate('/landscape')

      return
    }

    if(param_next_map && param_selected_character){
      if(param_selected_character == 'm' && !(currentCharacter == 'MILES')){
        if(config.TESTING){
          alert(`not next get ${param_selected_character} expected ${currentCharacter}`)
        }
        navigate('/wrong')
        return        
      }

      if(param_selected_character == 'p' && !(currentCharacter == 'PETER')){
        if(config.TESTING){
          alert(`not next get ${param_selected_character} expected ${currentCharacter}`)
        }
        navigate('/wrong')
        return        
      }

      let cmap = config.PATHS[currentCharacter].find(p => p.key === currentMap)
      
      if(cmap.next !== param_next_map){
        if(config.TESTING){
          alert(`not next get ${param_next_map} expected ${cmap.next}`)
        }
        navigate('/wrong')
        return        
      }

      if(cmap.final){
        navigate('/congratulations')
        return
      }
      
      navigate('/unlocked')
    }
  }

  return (
    <motion.div
      //initial={{y: -800}}
      //animate={{y: 0}}
      //exit={{y: -800}}
    >
     <LaunchLayout shadowBack fullBlack logo={!config.TESTING} onBack={() => {navigate('/choose')}}>
        <div className="h-screen pt-[100px] px-[25px] pb-[85px] flex flex-col justify-between text-white">
          <h1>Scan a <br></br> QR Code</h1>
          <div className='rounded-3xl shadow-[0px_0px_0px_4px_rgba(158,247,250,1)] m-8'>
            <QrScanner
              onResult={(result) => console.log(result)}
              onDecode={(result) => {
                scan(result)
              }}
              onError={(error) => {
                  console.log(error?.message)
                  setQRScannerError(error?.message)
                  forceCamera()
                }
              }
            />
          </div>
          <div className='text-center'>
            <p className="uppercase font-bold mb-5">Choose your adventure</p>
						<p className="">Scan one of the QR codes on the subway to explore the path of Peter or Miles</p>
            <img src={cameraImg} className={"w-[63px] mx-auto py-5"} onClick={() => {
							navigate('/scanqr')
						}}/>
          </div>
          {config.TESTING && <p>{qrScannerError}</p>}
          {config.TESTING && <>
            <h1>Fake scan</h1>

            <p>State:</p>
            {currentCharacter}{currentMap}

            <Button className="mb-4" title={"Character select peter"} onClick={() => {scan(config.BASE_URL + '/?m=1&p=p')}}/>
            <Button className="mb-4" title={"Character select miles"} onClick={() => {scan(config.BASE_URL + '/?m=1&p=m')}}/>

            <Button className="mb-4" title={"Scan miles map adidas"} onClick={() => {scan(config.BASE_URL + '/?nm=2&ps=m')}}/>
            <Button className="mb-4" title={"Scan miles map bait"} onClick={() => {scan(config.BASE_URL + '/?nm=3&ps=m')}}/>
            <Button className="mb-4" title={"Scan miles map warby"} onClick={() => {scan(config.BASE_URL + '/?nm=4&ps=m')}}/>
            
            <Button className="mb-4" title={"Scan peter map bait"} onClick={() => {scan(config.BASE_URL + '/?nm=2&ps=p')}}/>
            <Button className="mb-4" title={"Scan peter map adidas"} onClick={() => {scan(config.BASE_URL + '/?nm=3&ps=p')}}/>
            <Button className="mb-4" title={"Scan peter map warby"} onClick={() => {scan(config.BASE_URL + '/?nm=4&ps=p')}}/>


            <Button className="mb-4" title={"Scan bad qr"} onClick={() => {scan('not a url')}}/>
            <Button className="mb-4" title={"Clean"} onClick={() => {}}/>


          </>}
         </div>
     </LaunchLayout>
    </motion.div>
  )
}



