import React from 'react'
import LayoutWithSidebar from '../components/LayoutWithSidebar'
import LaunchLayout from '../components/LaunchLayout'


import Button from '../components/Button';

import { useNavigate,  } from "react-router-dom";

import config from '../config';

import {motion} from 'framer-motion'
import ReactPlayer from 'react-player'

import rotateImg from '../assets/images/icon-rotate-indication.png';
import cameraImg from '../assets/images/icon-camera.png';
import playImg from '../assets/images/icon-circle-play.png';
import logoImg from '../assets/images/logo-white.jpg';
import bgUnlocked from '../assets/images/bg-unlocked.png';


import { useAtom } from 'jotai'
import { atomWithStorage } from 'jotai/utils'

import { currentMapAtom, currentCharacterAtom, lastQRAtom } from '../atoms/index.js';

import { isLandscape } from '../libraries/utils'

export const Unlocked = (props) => {
	let navigate = useNavigate();
	const [currentMap, setCurrentMap] = useAtom(currentMapAtom)
	const [lastQR, setLastQRMap] = useAtom(lastQRAtom)
	const [currentCharacter, setCurrentCharacter] = useAtom(currentCharacterAtom)
  
	React.useEffect(() => {}, [])

	const onBack = () => {
		navigate('/maps')
	}

	const onNext = () => {
		if(currentMap && currentCharacter){
			let cmap = config.PATHS[currentCharacter].find(p => p.key === currentMap)
			setCurrentMap(cmap.next)

			if(!isLandscape()){
				navigate('/landscape')
			} else {
				navigate('/maps')
			}
		}
	}

	return (
		<motion.div
			//initial={{y: -800}}
			//animate={{y: 0}}
			//exit={{y: -800}}
		>
		 <LayoutWithSidebar>
			 <div className="h-screen p-[40px] pb-[85px]">
			 	<div className='relative'>
					<img src={bgUnlocked} className={"w-[500px] mx-auto"} />
					<div  className='absolute bottom-[50px] flex gap-8 justify-center left-0 right-0'>
						<Button 
							className="btn-white w-[138px]"
							title={"Back"}
							onClick={() => {onBack()}}
						/>
						<Button
							className="btn-white"
							title={"Head to the next location"}
							onClick={() => {onNext()}}
						/>
					</div>
				</div>
		 	</div>
		 </LayoutWithSidebar>
		</motion.div>
	)
}