import React from 'react'
import LaunchLayout from '../components/LaunchLayout'
import Button from '../components/Button';
import { useNavigate,  } from "react-router-dom";
import config from '../config';
import {motion} from 'framer-motion'
 

import rotateImg from '../assets/images/icon-rotate-indication.png';

export const Landscape = (props) => {
	let navigate = useNavigate();

	return (
		<motion.div
			//initial={{y: -800}}
			//animate={{y: 0}}
			//exit={{y: -800}}
		>
		 <LaunchLayout shadowBack fullBlack logo>
				<div className="h-screen p-[40px] pb-[85px] flex flex-col items-center justify-center text-white gap-8 text-center" onClick={()=>{navigate('/maps')}}>
					<img src={rotateImg} className={"w-[107px]"}/>
					<p>Rotate your device to landscape mode for optimal viewing experience.</p>
				</div>
		 </LaunchLayout>
		</motion.div>
	)
}